@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');
@import '../../module/utils.scss';

@function makelongshadow($color) {
  $val: 0px 0px $color;
  @for $i from 1 through 100 {
    $val: #{$val}, -#{$i}px #{$i}px #{$color};
  }
  @return $val;
}

//  Phone & Tablet
@media(max-width: 1023px){
  .main_wrapper{
    font-family: 'Noto Sans KR', sans-serif;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    padding: 15px 0px 25px 0px;
    margin-top: 80px;
    li{
      list-style-type: none;
    }
    a{
      text-decoration: none;
      color: rgb(0, 0, 0);
    }
    .main_contents{
      height: 100%;
      width: 95%;
      display: flex;
      justify-content: center;
      .main_contents_list{
        display: flex;
        flex-direction: column;
        padding: 0px 20px 0px 20px;
        .main_contents_list_element{
          display:flex;
          flex-direction: column;
          align-items: center;
          padding: 10px 0px;
          .link_wrapper{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 7px 0px;
            p{
              margin: 0;
              font-weight: bold;
              font-size: 20px;
            }
            a{
              font-size: 15px;
              text-align: center;
              padding: 0px 20px;
            }
          }
          .img_wrapper{
            width: 100%;
            .hov-anim{
              width: 100%;
              height: 300px;
            }
          }
        }
      }
    }
  }
}

@media(min-width: 1024px){
  .main_wrapper{
    font-family: 'Noto Sans KR', sans-serif;
    height: 100%;
    width: 100%;
    padding: 15px 0px 45px 0px;
    margin-top: 80px; //  Title 바가 fixed 상태이므로 바의 높이만큼 margin-top시켜줌
    li{
      list-style: none;
    }
    a{
      text-decoration: none;
      color: rgb(0, 0, 0);
    }
    .main_contents{
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      .main_contents_list{
        width: 100%;
        margin: 10px 20px 10px 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .main_contents_list_element{
          width: 80%;
          padding: 15px 0px;
          display:flex;
          justify-content: space-evenly;
          .link_wrapper{
            width: 300px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 7px 0px;
            p{
              font-size: 25px;
              font-weight: bold;
              margin: 0;
              padding: 0px 10px;
            }
            a{
              font-size: 25px;
              text-align: center;
              padding: 0px 10px;
            }
          }
          .img_wrapper{
            width: 50%;
            .hov-anim{
              width: 100%;
              height: 450px;
            }
          }
        }
      }
    }
  }
}

.main_background{
  background-color: $content-background;
  border-radius: 15px;
  margin-top: 45px;
  box-shadow: -5px -5px 30px 5px rgb(207, 75, 75), 5px 5px 15px 5px rgb(203, 83, 250);
}